.inline-popup-container {
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  flex-grow: 0; }
  .inline-popup-container .inline-popup-label {
    display: block;
    top: 0;
    left: 0;
    border: 1px solid transparent;
    width: 100%;
    height: 100%; }
    .inline-popup-container .inline-popup-label.disabled {
      cursor: not-allowed;
      color: var(--color-foreground-disabled); }
      .inline-popup-container .inline-popup-label.disabled:active, .inline-popup-container .inline-popup-label.disabled:hover, .inline-popup-container .inline-popup-label.disabled:focus {
        background-color: inherit; }
    .inline-popup-container .inline-popup-label:active, .inline-popup-container .inline-popup-label:hover, .inline-popup-container .inline-popup-label:focus {
      background-color: var(--color-state-hover); }
    .inline-popup-container .inline-popup-label:focus {
      outline-offset: -1px;
      outline: 1px dashed var(--color-accent); }
  .inline-popup-container.expanded .inline-popup-label {
    background-color: var(--color-state-selected);
    border-bottom: 0px;
    z-index: 6701; }
    .inline-popup-container.expanded .inline-popup-label:active, .inline-popup-container.expanded .inline-popup-label:hover, .inline-popup-container.expanded .inline-popup-label:focus {
      background-color: var(--color-state-selected-hover); }
  .inline-popup-container .inline-popup-panel {
    position: absolute;
    top: 100%;
    z-index: 6700;
    padding: 0.125rem;
    margin-top: -1px;
    background-color: var(--color-content-background-secondary);
    border: 1px solid var(--color-foreground-secondary);
    box-shadow: -3px 3px 10px -3px var(--color-dark-transparent-primary);
    border-radius: 2px; }
    .inline-popup-container .inline-popup-panel.left {
      left: 0; }
    .inline-popup-container .inline-popup-panel.right {
      right: 0; }
  :root[dir='rtl'] .inline-popup-container .inline-popup-panel.left {
    left: auto;
    right: 0; }
  :root[dir='rtl'] .inline-popup-container .inline-popup-panel.right {
    left: 0;
    right: auto; }

.inline-btn {
  background-color: transparent;
  cursor: pointer;
  height: 1.4em;
  width: 1.4em;
  padding: 0.2em;
  box-sizing: border-box;
  border: 1px solid transparent;
  flex-grow: 0;
  flex-shrink: 0;
  line-height: 1; }
