.balloon-dropdown {
  display: inline-flex;
  box-shadow: 0 8px 10px 0 var(--color-dark-transparent-tenth);
  background-color: var(--color-content-background-primary);
  border: 1px solid var(--color-foreground-secondary);
  max-width: 16.25rem;
  min-width: 12rem;
  border-radius: 2px; }
  .balloon-dropdown .balloon-inner-container {
    padding: 0.75rem;
    overflow: hidden;
