/**
 * General constants
 */
.label {
  font-family: var(--font-family-semibold);
  font-weight: 600; }

/**
 * Mixins
 */
.form-field {
  display: block; }

.label {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  color: inherit;
  margin-top: 0.125rem; }
  .required .label::after {
    content: ' *';
    color: var(--color-status-danger); }
  .label.required::after {
    content: ' *';
    color: var(--color-status-danger); }
  .label + .field-error {
    margin-top: 0.25rem; }
  .label.disabled {
    color: var(--color-foreground-disabled); }

.label-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
  .label-container .label-icon {
    margin-left: 0.5rem; }
    :root[dir='rtl'] .label-container .label-icon {
      margin-left: unset;
      margin-right: 0.5rem; }
  .label-container + .content {
    margin-top: 0.25rem; }

.label-farSide-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-end; }

.label-inner-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  flex: 1; }

.field-error {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 1.25rem;
  width: 100%;
  color: var(--color-status-danger);
  display: flex;
  flex-direction: row;
  font-size: var(--font-size-small);
  line-height: var(--line-height-small); }
  .field-error.hidden {
    display: none; }

.error-badge {
  margin-right: 0.25rem; }
  :root[dir='rtl'] .error-badge {
    margin-right: unset;
    margin-left: 0.25rem; }

.error-content {
  width: 100%; }
