/**
 * General constants
 */
.nav-container :global(.global-nav-item):global(.global-nav-item-active) {
  font-family: var(--font-family-semibold);
  font-weight: 600; }

/**
 * Mixins
 */
.masthead {
  flex-shrink: 0;
  flex-grow: 0;
  height: 3rem;
  display: flex;
  justify-content: space-between;
  color: var(--color-masthead-foreground);
  background-color: var(--color-masthead-background); }

.masthead-logo {
  display: flex;
  align-items: center;
  padding: 0.5rem 0 0.5rem 0.75rem; }
  :root[dir='rtl'] .masthead-logo {
    padding: 0.5rem 0.75rem 0.5rem 0.5rem; }

.masthead-branding {
  flex-grow: 1;
  max-width: calc(50% - 12.5rem);
  padding: 0px 1rem;
  font-size: var(--font-size-h3);
  font-weight: bold;
  min-width: 7.5rem;
  display: block;
  align-self: center; }
  @media (max-width: 639px) {
    .masthead-branding {
      max-width: 100%; } }

@media (max-width: 639px) {
  .search-input-container:not(.expanded) {
    display: none; } }

.search-input-container.expanded {
  max-width: unset; }

.masthead-toolbar-container {
  flex-grow: 1;
  max-width: 50%;
  position: relative; }

.masthead-toolbar {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  list-style-type: none; }
  @media (min-width: 640px) {
    .masthead-toolbar .search-button {
      display: none; } }
  @media (min-width: 480px) {
    .masthead-toolbar .more-button {
      display: none; } }
  .masthead-toolbar .more-button .masthead-toolbar-menu {
    padding: 0;
    background-color: var(--color-content-background-primary);
    color: var(--color-foreground-default);
    border: 0; }
    .masthead-toolbar .more-button .masthead-toolbar-menu ul {
      list-style: none;
      padding: 0;
      width: 12.5rem; }
      .masthead-toolbar .more-button .masthead-toolbar-menu ul .masthead-toolbar-btn-container {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: left; }
        .masthead-toolbar .more-button .masthead-toolbar-menu ul .masthead-toolbar-btn-container button {
          width: inherit;
          justify-content: left;
          padding: 0 1px; }
  @media (max-width: 479px) {
    .masthead-toolbar .masthead-toolbar-btn-container:not(.selected-more) {
      display: none; } }

.masthead-btn {
  background-color: transparent;
  width: 3rem;
  height: 3rem;
  justify-content: center;
  align-items: center;
  border: none;
  padding: 0;
  display: inline-flex;
  cursor: pointer;
  vertical-align: middle;
  align-content: center;
  color: inherit;
  color: var(--color-masthead-foreground); }
  .masthead-btn > div span {
    color: inherit; }
  .masthead-btn:focus, .masthead-btn:active {
    outline-offset: -1px;
    outline: 1px dashed var(--color-masthead-foreground); }
  .masthead-btn :before {
    width: var(--icon-size-standard);
    height: var(--icon-size-standard);
    vertical-align: baseline; }
  .masthead-btn.masthead-toolbar-btn:hover {
    background-color: var(--color-masthead-button-hover); }
  .masthead-btn.masthead-toolbar-btn.selected {
    background-color: var(--color-content-background-primary);
    color: var(--color-foreground-default); }
    .masthead-btn.masthead-toolbar-btn.selected:focus {
      outline: 1px dashed var(--color-accent); }
  @media (max-width: 481px) {
    .masthead-btn.masthead-toolbar-btn {
      color: var(--color-foreground-default); }
      .masthead-btn.masthead-toolbar-btn:hover {
        background-color: var(--color-state-hover); }
      .masthead-btn.masthead-toolbar-btn > span {
        padding: 1rem;
        width: 100%;
        justify-content: flex-start; } }
  @media (min-width: 480px) {
    .masthead-btn.masthead-toolbar-btn > span > span {
      display: none; } }

.nav-container {
  display: none;
  color: var(--color-masthead-foreground); }
  .nav-container .nav-panel {
    width: 12.5rem;
    padding: 0;
    border: 0; }
  .nav-container .nav-icon-collapsed {
    transform: rotate(90deg); }
  .nav-container .nav-icon-expanded {
    transform: rotate(-90deg); }
  .nav-container :global(.global-nav-item) {
    background-color: var(--color-content-background-primary);
    color: var(--color-foreground-default);
    border-left: 0.25rem solid transparent; }
    :root[dir='rtl'] .nav-container :global(.global-nav-item) {
      border-left: none;
      border-right: 0.25rem solid transparent; }
    .nav-container :global(.global-nav-item):active, .nav-container :global(.global-nav-item):hover {
      background-color: var(--color-content-background-primary);
      color: var(--color-foreground-default); }
    .nav-container :global(.global-nav-item):global(.global-nav-item-active) {
      background-color: var(--color-state-selected);
      border-color: var(--color-accent) !important; }
  .nav-container :global(.global-nav-item-container-title) {
    background-color: var(--color-content-background-primary);
    color: var(--color-foreground-default); }
  @media (max-width: 479px) {
    .nav-container {
      width: 3rem;
      display: initial; } }

.separator {
  border-top: 1px solid var(--color-state-selected-hover); }
