.action-trigger-container {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-family: var(--font-family-default);
  font-size: var(--icon-size-standard);
  line-height: 1.125rem;
  padding: 0.125rem 0.25rem;
  text-decoration: none;
  outline: none;
  border-radius: 2px; }
  .action-trigger-container.action-trigger-label-empty .label {
    margin: 0; }
  a:hover > .action-trigger-container {
    color: var(--color-accent-hover);
    background-color: var(--color-state-hover); }
  a:focus > .action-trigger-container {
    outline-offset: -1px;
    outline: 1px dashed var(--color-accent); }
    a:focus > .action-trigger-container:not(.disabled) {
      color: var(--color-accent-hover);
      background-color: var(--color-state-hover); }
  .action-trigger-container.disabled {
    color: var(--color-foreground-disabled); }
  .action-trigger-container .suffix {
    margin-left: 0.5rem; }
    .action-trigger-container .suffix::before {
      margin: 0; }
  .action-trigger-container .action-trigger-label {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: inline-block;
    line-height: 1.125rem;
    vertical-align: top;
    font-size: var(--font-size-default);
    margin-left: 0.5rem;
    max-width: 12.5rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; }
    :root[dir='rtl'] .action-trigger-container .action-trigger-label {
      margin: 0 0.5rem 0 0; }
    .action-trigger-container .action-trigger-label::before {
      vertical-align: top; }

.action-trigger-button {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  background: none;
  box-shadow: none;
  text-decoration: none;
  line-height: 0px;
  padding: 0.25rem;
  border-radius: 2px; }
  .action-trigger-button:focus {
    border: none;
    outline: none; }
  .action-trigger-button:not(:disabled) {
    cursor: pointer; }
  .action-trigger-button.disabled {
    cursor: not-allowed;
    color: var(--color-foreground-disabled); }
  .action-trigger-button:not(.disabled) .action-trigger-icon {
    color: var(--color-foreground-default); }
  .action-trigger-button:not(.disabled):hover, .action-trigger-button:not(.disabled):focus {
    background-color: var(--color-state-hover); }
  .action-trigger-button:not(.disabled):focus {
    outline-offset: -1px;
    outline: 1px dashed var(--color-accent); }
  .action-trigger-button:not(.disabled):active {
    background-color: var(--color-state-selected); }
