.panel {
  position: absolute;
  right: 0;
  height: 100%;
  min-height: 12.5rem;
  width: 20rem;
  display: flex;
  flex-direction: column;
  z-index: 6000;
  background-color: var(--color-content-background-primary);
  box-shadow: 0 25.6px 57.6px 0 var(--color-dark-transparent-tenth); }
  @media (max-width: 479px) {
    .panel {
      width: 100%; } }
  :root[dir='rtl'] .panel {
    right: unset;
    left: 0; }

.panel-container {
  padding: 1.25rem; }

.title {
  margin: 0; }

.header {
  display: flex;
  justify-content: space-between; }

.content {
  flex-grow: 1;
  flex-shrink: 1;
  overflow: auto;
  padding-top: 0; }
  .content.with-footer {
    padding-bottom: 0; }

.footer {
  flex-grow: 0;
  flex-shrink: 0;
  border-top: 1px solid var(--color-foreground-secondary); }
  .footer :global(.btn) {
    width: 7.5rem; }
