.combo-container {
  position: relative; }
  .combo-container .arrow {
    position: absolute;
    font-size: var(--font-size-small);
    height: var(--line-height-small);
    top: 0;
    bottom: 0;
    margin: auto 0;
    pointer-events: none;
    right: 0.5rem; }
    :root[dir='rtl'] .combo-container .arrow {
      right: unset;
      left: 0.5rem; }
    .combo-container .arrow.disabled {
      color: var(--color-foreground-disabled); }
  .combo-container .combo {
    width: 100%;
    height: 2rem;
    outline: none;
    appearance: none;
    border: 1px solid var(--color-foreground-secondary);
    background-color: var(--color-content-background-primary);
    border-radius: 2px;
    padding: 0 1.5rem 0 0.75rem;
    cursor: pointer; }
    :root[dir='rtl'] .combo-container .combo {
      padding: 0 0.75rem 0 1.5rem; }
    .combo-container .combo option {
      height: 2rem;
      cursor: pointer; }
      .combo-container .combo option.option-hidden {
        display: none; }
      .combo-container .combo option:checked {
        background-color: var(--color-state-selected); }
      .combo-container .combo option:disabled {
        border: 1px solid var(--color-foreground-disabled); }
      .combo-container .combo option:hidden {
        display: none; }
      .combo-container .combo option:hover, .combo-container .combo option:focus {
        background-color: var(--color-state-hover); }
    .combo-container .combo:active, .combo-container .combo:hover, .combo-container .combo:focus {
      border: 1px solid var(--color-accent); }
    .combo-container .combo:disabled {
      color: var(--color-foreground-disabled);
      border: 1px solid var(--color-foreground-disabled);
      background-color: var(--color-state-disabled);
      cursor: not-allowed; }
    .combo-container .combo.error:not(:disabled) {
      border: 1px solid var(--color-status-danger); }
