/**
 * Mixins
 */
@keyframes horizontal-loader {
  0% {
    opacity: 0;
    transform: translateX(0); }
  15% {
    opacity: 0;
    transform: translateX(0); }
  33% {
    opacity: 1;
    transform: translateX(calc(50% - 48px)); }
  66% {
    opacity: 1;
    transform: translateX(calc(50% - 48px)); }
  85% {
    opacity: 0;
    transform: translateX(100%); }
  100% {
    opacity: 0;
    transform: translateX(100%); } }

@keyframes horizontal-loader-rtl {
  0% {
    opacity: 0;
    transform: translateX(100%); }
  15% {
    opacity: 0;
    transform: translateX(100%); }
  33% {
    opacity: 1;
    transform: translateX(calc(50% + 48px)); }
  66% {
    opacity: 1;
    transform: translateX(calc(50% + 48px)); }
  85% {
    opacity: 0;
    transform: translateX(0); }
  100% {
    opacity: 0;
    transform: translateX(0); } }

.horizontal-loader {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  overflow: hidden;
  height: 20px;
  width: 100%; }

.horizontal-loader-inner {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: absolute;
  width: 100%;
  opacity: 0;
  display: flex;
  justify-content: flex-start; }
  :root[dir='rtl'] .horizontal-loader-inner {
    justify-content: flex-end; }

.horizontal-loader-inner > div {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 6px;
  background-color: var(--color-foreground-default); }

.horizontal-loader .horizontal-loader-inner:nth-child(1) {
  animation: horizontal-loader 3s 1000ms infinite ease-in-out;
  padding-left: 0px; }
  :root[dir='rtl'] .horizontal-loader .horizontal-loader-inner:nth-child(1) {
    animation: horizontal-loader-rtl 3s 1000ms infinite ease-in-out; }

.horizontal-loader .horizontal-loader-inner:nth-child(2) {
  animation: horizontal-loader 3s 800ms infinite ease-in-out;
  padding-left: 18px; }
  :root[dir='rtl'] .horizontal-loader .horizontal-loader-inner:nth-child(2) {
    animation: horizontal-loader-rtl 3s 800ms infinite ease-in-out; }

.horizontal-loader .horizontal-loader-inner:nth-child(3) {
  animation: horizontal-loader 3s 600ms infinite ease-in-out;
  padding-left: 36px; }
  :root[dir='rtl'] .horizontal-loader .horizontal-loader-inner:nth-child(3) {
    animation: horizontal-loader-rtl 3s 600ms infinite ease-in-out; }

.horizontal-loader .horizontal-loader-inner:nth-child(4) {
  animation: horizontal-loader 3s 400ms infinite ease-in-out;
  padding-left: 54px; }
  :root[dir='rtl'] .horizontal-loader .horizontal-loader-inner:nth-child(4) {
    animation: horizontal-loader-rtl 3s 400ms infinite ease-in-out; }

.horizontal-loader .horizontal-loader-inner:nth-child(5) {
  animation: horizontal-loader 3s 200ms infinite ease-in-out;
  padding-left: 72px; }
  :root[dir='rtl'] .horizontal-loader .horizontal-loader-inner:nth-child(5) {
    animation: horizontal-loader-rtl 3s 200ms infinite ease-in-out; }

.horizontal-loader .horizontal-loader-inner:nth-child(6) {
  animation: horizontal-loader 3s 0ms infinite ease-in-out;
  padding-left: 90px; }
  :root[dir='rtl'] .horizontal-loader .horizontal-loader-inner:nth-child(6) {
    animation: horizontal-loader-rtl 3s 0ms infinite ease-in-out; }
