:global(.md-dropdown).top-start.with-arrow:before {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  background-color: inherit;
  position: absolute;
  transform: rotateZ(45deg);
  bottom: calc(-0.25rem  - 1px);
  border-top: unset;
  border-left: unset;
  border-right: inherit;
  border-bottom: inherit;
  left: 0.75rem; }

