.dropdown-host {
  position: relative;
  overflow: hidden; }
  .dropdown-host :global(.md-dropdown-container) {
    visibility: hidden;
    pointer-events: none; }
  .dropdown-host :global(.md-dropdown) {
    position: absolute;
    visibility: hidden; }

:global(.md-dropdown-container) {
  visibility: visible;
  position: fixed;
  z-index: 9500; }
  :global(.md-dropdown-container) :global(.md-dropdown) {
    visibility: visible;
    pointer-events: none; }
    :global(.md-dropdown-container) :global(.md-dropdown).interactive {
      pointer-events: auto; }
