.icon-compact, .icon-standard, .icon-large {
  font-style: normal;
  font-weight: normal;
  font-family: var(--font-family-default);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 1px; }
  .icon-compact:before, .icon-standard:before, .icon-large:before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: inline-block;
    position: relative;
    font-family: var(--icon-font-family);
    text-decoration: none;
    vertical-align: middle;
    line-height: 100%; }
  .icon-compact > span, .icon-standard > span, .icon-large > span {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    margin-left: 0.5rem;
    margin-bottom: 0.125rem; }

.icon-compact.centered, .icon-standard.centered, .icon-large.centered, .icon-background.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.icon-compact {
  font-size: var(--icon-size-compact); }

.icon-standard {
  font-size: var(--icon-size-standard); }

.icon-large {
  font-size: var(--icon-size-large); }

.icon-background {
  width: var(--icon-size-standard);
  height: var(--icon-size-standard);
  border-radius: var(--icon-size-standard)/2; }
  .icon-background.icon-compact {
    width: var(--icon-size-compact);
    height: var(--icon-size-compact);
    border-radius: var(--icon-size-compact)/2; }
  .icon-background.icon-standard {
    width: var(--icon-size-standard);
    height: var(--icon-size-standard);
    border-radius: var(--icon-size-standard)/2; }
  .icon-background.icon-large {
    width: var(--icon-size-large);
    height: var(--icon-size-large);
    border-radius: var(--icon-size-large)/2; }
